.carousel-item :hover{cursor:pointer}.carousel-item h5{font-family:"Abel",sans-serif;font-size:1.9rem;color:#f5f5f5}.carousel-item img{width:75%;height:85vh;margin-left:13vw;object-fit:cover;object-position:center}.carousel-item p{font-family:"Poppins",sans-serif;font-size:1.3rem;color:#fff;font-size:1.1em}.carousel-inner{margin:auto}.carousel{background:#000}.ad-text-container{border-radius:10px;padding:10px;background-color:rgba(0,0,0,.575)}@media screen and (max-width: 740px){.ad-text-container h5{content:"röökiä"}.ad-text-container p{display:none}}@media screen and (max-width: 426px){.carousel-item img{width:100%;height:85vh;margin-left:0}.ad-text-container{font-size:.8em}}